<template>
  <div class="header__local">
    <a
      v-for="local in locales"
      :key="local"
      :class="{'active': $i18n.locale == local}"
      @click="setlocale(local)"
    > 
      {{local}}
    </a>
  </div>
</template>

<script>

import locales from '@/enums/locales'

export default {
  name: 'changelocal',
  data() {
    return {
      locales: locales
    }
  },
  methods: {
    setlocale(locale) {
      this.$i18n.locale = locale
      localStorage.setItem("locale", locale)
    }
  }
}
</script>

<style lang="scss" scoped>
  .header__local {
    margin: 0 0 0 1rem;
    a:not(:first-child)::before {
      content: '/';
      color: #868e96;
    }
    a.active {
      color: #000;
    }
  }
</style>
